.social-link {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 2rem;
	width: 2rem;
	border-radius: 50%;
	border: 0.1rem solid $white;
	transition: transform 0.3s;
	&:hover {
		transform: translateY(-0.3rem); }
	.icon {
		display: block;
		fill: $white;
		font-size: 1rem; }
	&--contacts {
		height: 2.7rem;
		width: 2.7rem;
		border-color: $theme;
		.icon {
			font-size: 1.4rem;
			fill: $theme; } } }

