.portfolio-block {
	&__content {
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding-right: 2.5rem;
		height: 100%;
		@include md {
			padding-right: 0; } }
	&__top {
		flex-grow: 1;
		display: flex;
		flex-direction: column;
		justify-content: center; }
	&__text {
		@include md {
			display: none; } }
	&__button {
		margin-top: 1rem;
		justify-self: flex-end;
		align-self: flex-end;
		@include md {
			display: none; }
		&--md {
			display: none;
			@include md {
				display: block;
				margin-top: 2.6rem; } } } }
