.project-gallery {
	$this: &;
	display: flex;
	flex-wrap: wrap;
	margin: -0.1rem -0.1rem 0;
	@include xs {
		margin: 0 -1.3rem 2.2rem; }
	&__item {
		width: 33.33%;
		padding: 0.1rem;
		@include sm {
			width: 50%; }
		@include xs {
			width: 100%; }
		img {
			@include coverdiv;
			object-fit: cover;
			@include xs {
				position: static; } }
		&:nth-child(9n+2) {
			#{$this}__image {
				padding-top: 91%; } }
		&:nth-child(9n+3) {
			#{$this}__image {
				padding-top: 120%; } }
		&:nth-child(9n+4) {
			#{$this}__image {
				margin-top: -20%;
				padding-top: 91%; } }
		&:nth-child(9n+5) {
			#{$this}__image {
				margin-top: -29%;
				padding-top: 120%; } }
		&:nth-child(9n+7) {
			#{$this}__image {
				margin-top: -29%;
				padding-top: 120%; } }
		&:nth-child(9n+8) {
			#{$this}__image {
				margin-top: -9%; } }
		&:nth-child(9n+9) {
			#{$this}__image {
				padding-top: 91%; } }
		&:nth-child(1), &:nth-child(2), &:nth-child(3) {
			#{$this}__image {
				margin-top: 0; } } }
	&__image {
		position: relative;
		padding-top: 100%;
		cursor: pointer;
		@include sm {
			padding-top: 110%!important;
			margin-top: 0!important; }
		@include xs {
			padding-top: 0!important; } } }
