.about {
	$this: &;
	display: flex;
	align-items: center;
	@include md {
		flex-direction: column-reverse; }
	&__image {
		width: 43%;
		padding-top: 43%;
		position: relative;
		@include md {
			width: 100%;
			padding-top: 115%; }
		img {
			@include coverdiv;
			object-fit: cover; } }
	&__content {
		width: 57%;
		padding-left: 4rem;
		@include md {
			width: 100%;
			padding-left: 0;
			margin-bottom: 2.7rem; } }
	&__title {
		width: 100%; }
	&__button {
		display: flex;
		justify-content: flex-end; }
	&__team {
		display: flex;
		flex-wrap: wrap;
		margin: -1rem; }
	&__team-item {
		width: 25%;
		padding: 1rem;
		@include md {
			width: 50%; }
		@include xs {
			width: 100%; } } }

.team-item {
	$this: &;
	cursor: pointer;
	position: relative;
	padding-top: 150%;
	overflow: hidden;
	&:hover {
		#{$this}__label {
			opacity: 1; }
		img {
			transform: scale(1.1); } }
	img {
		@include coverdiv;
		object-fit: cover;
		transform-origin: 50% 50%;
		transition: transform 1s;
		will-change: transform; }
	&__label {
		@include coverdiv;
		z-index: 1;
		opacity: 0;
		transition: opacity 0.25s ease-in-out;
		color: $white;
		background-color: rgba(0,0,0,0.25);
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 2rem; } }
