.logo {
	display: block;
	font-size: 5.1rem;
	position: relative;
	@include xs {
		font-size: 5.5rem; }
	@include r(374) {
		font-size: 4.5rem; }
	.icon {
		display: block;
		fill: $darkLogo;
		transition: height 0.3s, width 0.3s; }
	&__full {
		display: block;
		transition: opacity 0.3s; }
	&__scrolled {
		display: block;
		position: absolute;
		top: 0;
		left: 0; }
	&--white {
		.icon {
			fill: $white; } }
	&--lg {
		font-size: 5.6rem; } }
