.section {
	padding: 6rem 0 4rem;
	@include md {
		padding: 3rem 0; }
	&--sm-flex {
		@include sm {
			display: flex;
			flex-direction: column; } }
	&--pt-0 {
		padding-top: 0;
		margin-top: -1px; }
	&--pb-0 {
		padding-bottom: 0; }
	&--pb-m {
		padding-bottom: 11.8rem;
		@include xs {
			padding-bottom: 7.3rem; } }
	&--md-pt-m {
		@include md {
			padding-top: 3.7rem; } } }

.section-type {
	letter-spacing: 0.1rem;
	@include medium;
	font-size: 1.3rem;
	color: $gayText;
	margin-bottom: 0.5rem; }

.section-header {
	margin-bottom: 1.1rem;
	@include xs {
		margin-bottom: 1.8rem; }
	&--white-bg {
		background-color: $white; }
	&--mb-m {
		margin-bottom: 3.5rem;
		@include md {
			margin-bottom: 3rem; }
		@include xs {
			margin-bottom: 2.8rem; } }
	&--p-m {
		padding: 3rem;
		margin-bottom: 0; } }

.section-title {
	$this: &;
	font-size: 2.6rem;
	@include book;
	letter-spacing: 0.05rem;
	a {
		color: $theme; }
	&--mb {
		margin-bottom: 12px; }
	&--center {
		text-align: center; }
	&--decoration {
		position: relative;
		z-index: 1;
		&:before {
			content: '';
			position: absolute;
			top: 50%;
			left: 0;
			width: 10rem;
			height: 1px;
			background-color: $black;
			transform: translate(-100%, -50%); }
		.icon {
			position: absolute;
			top: 50%;
			left: -10rem;
			transform: translate(-100%, -50%);
			font-size: 2.2rem;
			fill: $black; } }
	&--decoration-white {
		&:before {
			background-color: $white;
			@include md {
				display: none; } }
		.icon {
			fill: $white;
			@include md {
				display: none; } } }
	&--decoration-right {
		@include md {
			display: flex;
			flex-direction: row-reverse;
			align-items: center; }
		&:before {
			left: auto;
			right: -5rem;
			transform: translate(0, -50%);
			@include md {
				margin: 0 2.2rem 0;
				position: static;
				flex-grow: 1;
				//right: 2.2rem
 } }				//width: 100%
		.icon {
			left: auto;
			right: -9.4rem;
			@include md {
				right: 0;
				transform: translate(0, -50%); } } }
	&--line {
		display: flex;
		justify-content: space-between;
		.icon {
			font-size: 2.2rem;
			display: block; } }
	&--line-center {
		justify-content: center; }
	&__line {
		flex-grow: 1;
		display: flex;
		justify-content: flex-end;
		align-items: center;
		padding-left: 2rem;
		&:before {
			content: '';
			height: 1px;
			background-color: #231F20;
			flex-grow: 1; }
		&--reverse {
			flex-direction: row-reverse;
			padding-left: 0;
			padding-right: 2rem;
			@include xs {
				display: none; } } } }




