.page {
	padding-top: 6.8rem;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	position: relative;
	overflow: hidden;
	&__decor-lines {
		position: absolute;
		left: 3rem;
		top: 0;
		height: 100%;
		min-height: 100vh;
		width: calc(100% - 6rem);
		z-index: -1;
		@include xs {
			display: none; }
		&:before, &:after {
			content: '';
			position: absolute;
			top: 0;
			height: 100%;
			width: 1px;
			background-color: $decorationLinesColor; }
		&:before {
			left: 33.33%; }
		&:after {
			right: 33.33%; } } }

