.contact-form {
	background-color: $theme;
	margin: -1px;
	color: $white;
	padding: 2.5rem 3.8rem 0;
	@include xs {
		padding: 2.5rem 1.7rem 0; }
	&__title {
		@include book;
		font-size: 1.8rem; }
	&__row {
		display: flex;
		margin: 0 -1.4rem;
		@include xs {
			flex-wrap: wrap; } }
	&__item {
		width: 33.33%;
		padding: 1.4rem;
		@include xs {
			width: 50%; }
		&--lg {
			width: 66.66%;
			@include xs {
				width: 100%; } }
		&--xs-full {
			@include xs {
				width: 100%; } } }
	&__button {
		display: flex;
		justify-content: flex-end;
		flex-grow: 1;
		button {
			color: $white;
			padding: 1.4rem;
			font-size: 1.4rem;
			@include medium;
			background-color: transparent;
			display: flex;
			align-items: center;
			&:hover {
				.icon {
					transform: translateX(0.5rem); } }
			.icon {
				font-size: 1.4rem;
				margin-bottom: -0.2rem;
				fill: $white;
				margin-left: 1.2rem;
				transition: transform 0.3s; } } } }

