.main-slider {
	font-size: 0;
	.slick-dots {
		pointer-events: auto; }
	.btn-slick {
		pointer-events: auto;
		display: flex;
		align-items: center;
		justify-content: center;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		height: 32px;
		width: 32px;
		border-radius: 50%;
		border: 1px solid $white;
		background-color: transparent;
		z-index: 1;
		transition: background-color 0.3s, border-color 0.3s;
		@include r(1440) {
			height: 30px;
			width: 30px; }
		@include sm {
			background-color: $theme;
			border-color: $theme; }
		.no-touch &:hover {
			background-color: $theme;
			border-color: $theme; }
		.icon {
			font-size: 20px;
			fill: white;
			display: block;
			@include r(1440) {
				font-size: 16px; } }
		&--prev {
			left: 1.6rem;
			.icon {
				left: 0.4rem; } }
		&--next {
			right: 1.6rem;
			.icon {
				right: 0.4rem; } } }
	&__slide {
		padding-top: 76.4%;
		position: relative;
		@include xs {
			padding-top: 112%; }
		img {
			@include coverdiv;
			object-fit: cover; } }
	&--theme {
		.slick-dots li button:before {
			border-color: $theme; }
		.slick-dots li.slick-active button:before {
			background-color: $theme; }
		.btn-slick {
			border-color: $theme;
			@include sm {
				background-color: transparent; }
			.icon {
				fill: $theme; }
			.no-touch &:hover {
				.icon {
					fill: $white; } } } } }
