.step {
	height: 100%;
	position: relative;
	text-align: center;
	padding: 4.6rem 1rem 4.2rem;
	&__arrow {
		height: 3rem;
		width: 3rem;
		border-radius: 50%;
		border: 1px solid $lightGray2;
		background-color: $white;
		display: flex;
		align-items: center;
		justify-content: center;
		position: absolute;
		right: -1px;
		top: 50%;
		transform: translate(50%, -50%);
		font-size: 1.5rem;
		z-index: 1;
		.icon {
			fill: $lightGray2; } }
	&__number {
		overflow: hidden;
		z-index: -1;
		@include medium;
		font-size: 25.2rem;
		color: $lightGray;
		position: absolute;
		top: 0;
		left: 1.5rem;
		line-height: 0.67;
		@include xs {
			top: auto;
			bottom: 0;
			overflow: visible;
			font-size: 20.5rem; } }
	&__title {
		font-size: 1.8rem;
		@include book;
		max-width: 26rem;
		margin: 0 auto; }
	&__icon {
		font-size: 3rem;
		margin-bottom: 1rem;
		display: flex;
		justify-content: center;
		.icon {
			display: block;
			fill: $theme; } } }


