html {
	font-size: 14px;
	@include r(1680) {
		font-size: 0.834vw; }
	@include md {
		font-size: 10px; } }

body {
	font-size: 1.8rem;
	line-height: 1.1;
	background-color: $white;
	color: $textColor;
	@include light;
	letter-spacing: 0.04rem;
	&.is-overflow {
		overflow: hidden;
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100vh;
		height: calc(var(--vh) * 100); } }

input, textarea {
	border-radius: 0; }

strong {
	@include book; }

b {
	@include medium; }

p {
	margin: 1rem 0; }

.container {
	max-width: 120rem;
	padding: 0 3rem;
	margin: 0 auto;
	position: relative;
	@include xs {
		padding: 0 1.3rem; } }

.text {
	letter-spacing: 0.15rem;
	line-height: 1.2;
	b, strong {
		@include book; }
	a {
		color: $theme; }
	h1, h2, h3, h4 {
		margin-bottom: 2rem; }
	ul {
		margin-bottom: 2rem;
		li {
			&:before {
				content: "- "; } } }
	p {
		margin-bottom: 1rem;
		& + p {
			margin-top: 2.1rem; }
		& + h1, & + h2, & + h3, & + h4 {
			margin-top: 2.5rem; } }
	@include xs {
		letter-spacing: 0.04rem;
		line-height: 1; } }

.row {
	display: flex;
	flex-wrap: wrap;
	&--sm-order-1 {
		@include sm {
			order: 1; } }
	&--column-reverse-md {
		@include md {
			flex-direction: column-reverse; } }
	&--center {
		align-items: center; }
	&--between {
		justify-content: space-between; }
	&--justify-center {
		justify-content: center; }
	&--padding-sm {
		.col {
			padding: 0.2rem; } }
	&--bordered {
		border: 1px solid $decorationLinesColor;
		& + & {
			margin-top: -1px; }
		@include xs {
			border: none;
			& + & {
				margin-top: 0; } } }
	&--bordered-xs {
		@include xs {
			border: 1px solid $decorationLinesColor;
			& + & {
				margin-top: -1px; } } }
	&--xs-mb-m {
		@include xs {
			padding-bottom: 3.1rem; } } }

.col-2 {
	width: 25%; }

.col-3 {
	width: 33.33%; }

.col-4 {
	width: 50%; }

.col-5 {
	width: 66.66%; }

.col-6 {
	width: 75%; }

.col-md {
	@include md {
		width: 100%; } }

.col-3-md {
	@include md {
		width: 33.33%; } }

.col-xs {
	@include xs {
		width: 100%; } }

.col-xs-border {
	@include xs {
		border: 1px solid $decorationLinesColor;
		& + & {
			margin-top: -1px; } } }



