.projects {
	display: flex;
	flex-wrap: wrap;
	margin: -0.1rem -0.1rem 0;
	&__item {
		width: 33.33%;
		padding: 0.1rem;
		position: relative;
		@include xs {
			width: 100%; }
		.project-preview {
			padding-top: 100%;
			@include xs {
				margin-top: 0!important; } }
		&:nth-child(9n+2) {
			.project-preview {
				padding-top: 91%; } }
		&:nth-child(9n+3) {
			.project-preview {
				padding-top: 120%; } }
		&:nth-child(9n+4) {
			.project-preview {
				margin-top: -20%;
				padding-top: 91%; } }
		&:nth-child(9n+5) {
			.project-preview {
				margin-top: -29%;
				padding-top: 120%; } }
		&:nth-child(9n+6) {
			.project-preview {
				//margin-top: -29%
 } }				//padding-top: 120%
		&:nth-child(9n+7) {
			.project-preview {
				margin-top: -29%;
				padding-top: 120%; } }
		&:nth-child(9n+8) {
			.project-preview {
				margin-top: -9%; } }
		&:nth-child(9n+9) {
			.project-preview {
				padding-top: 91%; } }
		&:nth-child(1), &:nth-child(2), &:nth-child(3) {
			.project-preview {
				margin-top: 0; } } } }

