.project-preview {
	$this: &;
	text-decoration: none;
	color: $textColor;
	display: block;
	padding-top: 140%;
	overflow: hidden;
	position: relative;
	@include xs {
		padding-top: 120%; }
	&:hover {
		#{$this}__poster {
			transform: scale(1); }
		#{$this}__link {
			transform: translateY(0);
			@include sm {
				max-width: 20rem; }
			@include xs {
				max-width: 20rem; } } }
	&__poster {
		@include coverdiv;
		display: block;
		background-position: center;
		background-size: cover;
		transform: scale(1.1);
		transition: transform 2s;
		@include md {
			transform: scale(1); } }
	&__link {
		position: absolute;
		bottom: 0;
		left: 0;
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		max-width: 25rem;
		background-color: $white;
		padding: 1rem;
		transform: translateY(100%);
		transition: transform 0.3s;
		font-size: 1.6rem;
		@include book;
		.icon {
			font-size: 1.2rem; }
		@include md {
			transform: none;
			max-width: 90%; }
		@include xs {
			max-width: 26rem; } } }
