.language {
	$this: &;
	position: relative;
	font-size: 1.2rem;
	cursor: pointer;
	@include sm {
		padding: 0.8rem;
		font-size: 1.4rem; }
	&.inside-menu {
		@include sm {
			z-index: 9;
			position: absolute;
			top: 2.7rem;
			left: 1.2rem;
			#{$this}__active {
				color: $black; }
			#{$this}__list {
				right: auto;
				left: 100%; }
			#{$this}__item {
				color: $black;
				margin-left: 1rem;
				margin-right: 0; } } }
	&.is-active {
		#{$this}__list {
			pointer-events: auto;
			opacity: 1; } }
	&__active {
		@include bold;
		color: $theme;
		@include sm {
			@include medium;
			color: $white; } }
	&__list {
		position: absolute;
		right: 100%;
		top: 0;
		@include book;
		display: flex;
		opacity: 0;
		pointer-events: none;
		transition: opacity 0.3s;
		@include md {
			right: -2rem;
			top: 100%;
			flex-direction: column;
			background-color: $white;
			text-align: center;
			padding: 2rem; }
		@include sm {
			background-color: transparent;
			padding: 0;
			text-align: left;
			flex-direction: row;
			right: 100%;
			top: 0; } }
	&__item {
		text-decoration: none;
		margin-right: 0.9rem;
		color: $black;
		transition: color 0.3s;
		.no-touch &:hover {
			color: $theme; }
		@include md {
			margin-right: 0;
			&:not(:last-child) {
				margin-bottom: 1rem; } }
		@include sm {
			color: $white;
			margin-right: 0.5rem;
			padding: 0.8rem;
			&:not(:last-child) {
				margin-bottom: 0; } } } }
