.contacts-block {
	padding: 2.8rem 2.9rem 2.2rem;
	@include xs {
		padding: 2.8rem 1.9rem 2.2rem; }
	@include r(374) {
		padding: 2rem 1.2rem 2rem; }
	&--center {
		display: flex;
		justify-content: center;
		padding: 4.8rem 2.9rem;
		@include md {
			padding: 1.6rem; } }
	&__item {
		margin-bottom: 1rem; }
	&__socials {
		margin-top: 1.7rem; } }

