.soon {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	min-height: 100vh;
	min-height: calc(var(--vh) * 100);
	&__logo {
		font-size: 10rem;
		margin-bottom: 3rem; }
	&__title {
		@include book;
		font-size: 5rem;
		margin-bottom: 2rem; }
	&__contacts {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: 3rem; } }
