.back-link {
	display: flex;
	align-items: center;
	@include medium;
	color: $black;
	font-size: 1.4rem;
	letter-spacing: 0.15rem;
	text-decoration: none;
	transition: color 0.3s;
	&:hover {
		color: $theme;
		.icon {
			fill: $theme; } }
	.icon {
		font-size: 1.2rem;
		margin-right: 1rem;
		transition: fill 0.3s; } }
