.burger {
	$this: &;
	padding: 0.3rem 0;
	&.is-active {
		#{$this}__line {
			background-color: $black;
			&:nth-child(1) {
				transform: translate(0, 0.7rem) rotate(45deg); }
			&:nth-child(2) {
				transform: scaleX(0); }
			&:nth-child(3) {
				transform: translate(0, -0.7rem) rotate(-45deg); } } }
	&__line {
		width: 3rem;
		display: block;
		height: 0.2rem;
		background-color: $white;
		transition: transform 0.3s;
		transform-origin: 50% 50%;
		&:not(:last-child) {
			margin-bottom: 0.5rem; } } }
