.table {
	background-color: $white;
	overflow-x: auto;
	margin: 0 -0.5rem;
	&__row {
		min-width: 340px;
		display: flex;
		align-items: center;
		border-bottom: 1px solid $decorationLinesColor;
		&:nth-child(even) {
			background-color: #f1f1f161; }
		&:first-child {
			@include book; } }
	&__col {
		padding: 0.5rem;
		&:nth-child(1) {
			width: 70%; }
		&:nth-child(2) {
			text-align: center;
			width: 30%;
			display: flex;
			align-items: center;
			justify-content: center; } } }

.mark {
	display: inline-block;
	transform: rotate(45deg);
	height: 24px;
	width: 12px;
	border-bottom: 5px solid $theme;
	border-right: 5px solid $theme; }
