.project-hero {
	display: flex;
	@include md {
		display: block; }
	&__content {
		width: 29.2%;
		padding: 3rem 2rem 2rem 0;
		@include md {
			width: 100%;
			padding: 3rem 0 0;
			margin-bottom: 2rem; } }
	&__image {
		width: 70.8%;
		position: relative;
		padding-top: 52.3%;
		@include md {
			width: auto;
			margin: 0 -3rem;
			padding-top: 60%; }
		@include sm {
			display: none; }
		img {
			@include coverdiv;
			object-fit: cover; } }
	&__link {
		margin-bottom: 5rem;
		@include md {
			margin-bottom: 2.2rem; } } }
