.p-info {
	margin-top: -1px;
	background-color: $white;
	display: none;
	@include xs {
		margin-bottom: 3rem; }
	&.is-active {
		display: block; }
	&__title {
		text-transform: uppercase;
		font-size: 1.6rem;
		padding: 2rem 1rem;
		text-align: center;
		border-left: 1px solid $decorationLinesColor;
		border-bottom: 1px solid $decorationLinesColor;
		margin-left: -1px;
		@include md {
			margin-left: 0;
			border-left: 0;
			margin-top: -1px;
			border-top: 1px solid $decorationLinesColor; }
		@include xs {
			padding: 1rem; } }
	&__price-section {
		padding: 3rem 0.8rem 3rem 1.5rem;
		@include r(365) {
			padding: 2rem 0.8rem;
			font-size: 1.6rem; } }
	&__additional-text {
		@include light;
		font-size: 1.6rem;
		margin-bottom: 2rem; }
	&__price-list {
		margin-bottom: 2rem;
		line-height: 1.4; }
	&__price-item {
		display: flex;
		justify-content: space-between;
		&:before {
			content: "●";
			margin-right: 0.7rem;
			color: $theme; } }
	&__price-main {
		flex-grow: 1; }
	&__price-value {
		margin-left: 1rem;
		width: 20.8rem;
		@include light;
		@include r(365) {
			width: auto;
			text-align: right; }
		span {
			@include book;
			color: $theme; } }
	&__price-btn {
		margin-bottom: 2rem; }
	&__buttons {
		display: none;
		@include xs {
			display: flex; } }
	&__button {
		width: 50%;
		background-color: transparent;
		display: block;
		border: 1px solid $theme;
		text-align: center;
		font-size: 1.4rem;
		text-transform: uppercase;
		@include book;
		padding: 1.7rem 0.5rem;
		transition: background-color 0.3s, color 0.3s;
		&.is-active {
			color: $white;
			background-color: $theme; } }
	&__project-tab {
		@include xs {
			display: none;
			&.is-active {
				display: block; } } }
	&__desc {
		border-left: 1px solid $decorationLinesColor;
		margin-left: -1px;
		padding: 3rem 1.9rem 5rem;
		line-height: 1.5;
		ol {
			list-style: none;
			counter-reset: counter;
			li {
				counter-increment: counter;
				&:before {
					content: counter(counter) ". ";
					@include book; } } }
		p + p {
			margin-top: 1rem; }
		ul {
			li {
				&:before {
					content: "- "; } } } } }
