.hero {
	&__content {
		margin-bottom: 2.2rem;
		@include xs {
			margin-bottom: 0; } }
	&__title {
		position: relative;
		z-index: 1;
		@include book;
		font-size: 3.4rem;
		margin-right: -15rem;
		margin-bottom: 1rem;
		letter-spacing: 0.07rem;
		@include xs {
			font-size: 2.6rem;
			letter-spacing: 0; }
		span {
			background-color: $white;
			padding-right: 3rem;
			@include md {
				background-color: transparent;
				padding-right: 0; } } }
	&__desc {
		letter-spacing: 0.15rem;
		margin-bottom: 3.4rem; }
	&__slider {
		@include md {
			margin: 0 -3rem 3rem; }
		@include xs {
			margin: 0 -1.3rem 1.9rem; } } }

