.footer {
	background-color: $footerBg;
	padding: 3rem 0 2.4rem;
	height: 12.2rem;
	overflow: hidden;
	@include md {
		height: auto; }
	&__logo {
		margin-top: -1.3rem; }
	&__inner {
		display: flex;
		justify-content: space-between;
		@include md {
			flex-wrap: wrap; } }
	&__nav {
		@include md {
			margin: 2rem 0 1.1rem;
			width: 100%; } }
	&__contacts {
		display: flex;
		@include md {
			display: block;
			margin-right: 1rem; }
		@include r(374) {
			margin-bottom: 2rem; } }
	&__contacts-item {
		&:not(:last-child) {
			margin-right: 3.4rem;
			@include md {
				margin-right: 0;
				margin-bottom: 1rem; } } } }
