.steps-list {
	display: flex;
	flex-wrap: wrap;
	position: relative;
	z-index: 1;
	@include sm;
	background-color: $white;
	&:after {
		content: '';
		position: absolute;
		width: 100vw;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
		border-top: 1px solid $decorationLinesColor;
		@include sm {
			width: 100%; } }
	&__item {
		width: 33.33%;
		@include sm {
			width: 50%;
			border-left: 1px solid $decorationLinesColor;
			border-right: 1px solid $decorationLinesColor;
			.step__arrow {
				display: none; } }
		&:nth-child(3n-1) {
			position: relative;
			&:after {
				content: '';
				position: absolute;
				width: 100vw;
				top: 0;
				left: 50%;
				transform: translateX(-50%);
				border-top: 1px solid $decorationLinesColor;
				@include sm {
					display: none; } } }
		&:nth-child(2n-1) {
			@include sm {
				margin-right: -1px;
				position: relative;
				&:after {
					display: block;
					content: '';
					position: absolute;
					width: 200%;
					top: 0;
					left: 0;
					transform: translateX(0);
					border-top: 1px solid $decorationLinesColor; } } }
		&:nth-child(n+4) {
			.step__number {
				left: auto;
				right: 1rem; } }
		&:nth-child(n+3) {
			@include sm {
				.step__number {
					left: auto;
					right: 1rem; } } }
		&:nth-child(n+5) {
			@include sm {
				.step__number {
					left: 1.5rem;
					right: auto; } } }
		&:nth-child(1) {
			@include sm {
				.step__arrow {
					display: flex;
					top: 100%;
					transform: translate(50%, -50%) rotate(90deg); } } }
		&:nth-child(4) {
			@include sm {
				.step__arrow {
					display: flex;
					right: auto;
					left: -1px;
					top: 100%;
					transform: translate(-50%, -50%) rotate(90deg); } } } } }

