.job-type {
	$this: &;
	display: flex;
	flex-direction: column;
	align-items: center;
	text-decoration: none;
	color: $textColor;
	padding: 4rem 5.5rem;
	position: relative;
	@include md {
		padding: 2rem 1.5rem; }
	@include xs {
		flex-direction: row;
		padding: 1.4rem 0.7rem 1.3rem 1.7rem; }
	&.is-active, &.is-active:hover {
		background-color: $theme;
		#{$this}__title {
			color: $white; }
		#{$this}__icon {
			&:before {
				background-color: $white; }
			.icon {
				fill: $white; } } }
	&--button {
		padding: 4.6rem 5.5rem 4.4rem;
		@include md {
			padding: 2rem 1.5rem 4.4rem; }
		@include xs {
			flex-direction: column;
			padding: 5rem 5.5rem 4.9rem; }
		#{$this}__icon {
			@include xs {
				margin-right: 0;
				margin-bottom: 1rem;
				width: auto; } }
		#{$this}__title {
			margin-bottom: 1.5rem;
			@include xs {
				font-size: 2rem;
				text-align: center;
				br {
					display: block; } } } }
	&--sm {
		padding: 2.2rem 5.5rem;
		@include md {
			padding: 2.2rem 2rem; } }
	&:hover {
		#{$this}__title {
			color: $theme; }
		#{$this}__icon {
			transform: scale(1.1);
			.icon {
				fill: $theme; } } }
	&__icon {
		display: inline-block;
		margin-bottom: 1.5rem;
		position: relative;
		transition: transform 0.3s;
		@include xs {
			margin-right: 2rem;
			margin-bottom: 0;
			width: 3.3rem;
			&:before {
				display: none; } }
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: -0.3rem;
			transform: translate(-100%, -50%);
			height: 1rem;
			width: 1rem;
			border-radius: 50%;
			background-color: $theme; }
		.icon {
			fill: $textColor;
			font-size: 3.5rem;
			display: block;
			transition: fill 0.3s; } }
	&__title {
		@include book;
		text-align: center;
		font-size: 2rem;
		transition: color 0.3s;
		letter-spacing: 0.04rem;
		@include sm {
			font-size: 1.6rem; }
		@include xs {
			font-size: 1.8rem;
			text-align: left;
			br {
				display: none; } } }
	&__button {
		position: absolute;
		bottom: 1.7rem;
		right: 0; } }
