.modal {
	display: none;
	z-index: 99;
	position: fixed;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	background-color: rgba(0, 0, 0, 0.8);
	&.is-active {
		display: block; }
	&__inner {
		pointer-events: none;
		@include vcenter;
		background-color: transparent;
		width: 80%;
		@include xs {
			width: 100%; } }
	&__close {
		position: absolute;
		top: 0;
		right: -1.7rem;
		transform: translateX(100%);
		background-color: transparent;
		font-size: 1.6rem;
		@include xs {
			right: 1.5rem;
			top: -1.5rem;
			transform: translateY(-100%); }
		.icon {
			fill: $white; } }
	&__slider-item {
		position: relative;
		text-align: center;
		img {
			margin: 0 auto;
			max-width: 100%;
			max-height: 90vh;
			max-height: calc(var(--vh) * 90); } } }
