.header {
	z-index: 9;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	padding: 0.9rem 0 0.8rem;
	background-color: $white;
	transition: padding 0.3s;
	border-bottom: 1px solid $decorationLinesColor;
	.container {
		position: static; }
	@include sm {
		background-color: $theme;
		border-bottom: none;
		padding: 0.9rem 0 0.6rem;
		.logo .icon {
			fill: $white; } }
	&.is-scrolling {
		padding: 0.6rem 0;
		box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.1);
		.logo {
			font-size: 3rem;
			&__full {
				opacity: 0; } } }
	&__inner {
		display: flex;
		align-items: center;
		justify-content: space-between; }
	&__lang {
		@include sm {
			flex-grow: 1;
			display: flex;
			justify-content: flex-end;
			margin-right: 2rem; }
		@include r(374) {
			margin-right: 1rem; } }
	&__nav-logo {
		display: none;
		font-size: 5rem;
		position: absolute;
		top: 1.5rem;
		left: 50%;
		transform: translateX(-50%);
		@include sm {
			display: block; } }
	&__nav {
		flex-grow: 1;
		display: flex;
		justify-content: flex-end;
		margin-right: 10.3rem;
		transition: opacity 0.3s;
		@include md {
			margin-right: 3rem; }
		@include sm {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100vh;
			height: calc(var(--vh) * 100);
			overflow: auto;
			background-color: $white;
			z-index: 2;
			display: block;
			margin-right: 0;
			padding-top: 12.6rem;
			padding-bottom: 4rem;
			opacity: 0;
			pointer-events: none;
			&.is-active {
				display: block;
				pointer-events: auto;
				opacity: 1; } } }
	&__burger {
		position: relative;
		display: none;
		z-index: 3;
		@include sm {
			display: block; } } }
