.contact-item {
	$this: &;
	letter-spacing: 0.14rem;
	display: flex;
	align-items: center;
	font-size: 1.8rem;
	text-decoration: none;
	color: $black;
	&:hover {
		#{$this}__icon {
			transform: translateY(-0.3rem); } }
	&--footer {
		@include book;
		font-size: 1.3rem;
		color: $white;
		#{$this}__icon {
			.icon {
				fill: $white;
				font-size: 1.1rem; } } }
	&__icon {
		width: 1.8rem;
		display: flex;
		justify-content: center;
		margin-right: 1.1rem;
		transition: transform 0.3s;
		.icon {
			font-size: 1.6rem;
			display: block;
			fill: $theme; } } }
