.nav {
	display: flex;
	align-items: center;
	font-size: 1.2rem;
	letter-spacing: 0.05rem;
	@include sm {
		flex-direction: column;
		font-size: 2rem; }
	&__item {
		&:not(:last-child) {
			margin-right: 4rem;
			@include md {
				margin-right: 1.5rem; }
			@include sm {
				margin-right: 0;
				margin-bottom: 3.4rem; } }
		a {
			text-decoration: none;
			color: $black;
			@include book;
			&.is-active, &:hover {
				color: $theme;
				@include sm {
					@include medium; } } }
		&.current-menu-item {
			a {
				color: $theme;
				@include sm {
					@include medium; } } } } }
