.btn {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	text-decoration: none;
	color: $textColor;
	@include medium;
	font-size: 1.4rem;
	padding: 1.1rem 1.8rem;
	transition: background-color 0.3s, color 0.3s;
	letter-spacing: 0.2rem;
	.icon-arrow-right {
		fill: $black;
		margin-left: 1rem;
		font-size: 1.4rem;
		margin-bottom: -0.2rem;
		transition: fill 0.3s, transform 0.3s; }
	&:hover {
		background-color: $theme;
		color: $white;
		.icon-arrow-right {
			fill: $white;
			transform: translateX(0.5rem); } }
	@include md {
		background-color: $theme;
		color: $white;
		.icon-arrow-right {
			fill: $white;
			transform: translateX(0.5rem); } }
	&--transparent {
		padding: 1.1rem 0;
		@include md {
			padding: 1.1rem 1.2rem; }
		&:hover {
			background-color: transparent;
			color: $theme;
			.icon-arrow-right {
				fill: $theme; } } }
	&--transparent-md {
		@include md {
			color: $textColor;
			background-color: transparent;
			.icon-arrow-right {
				fill: $black; } } }
	&--reverse {
		color: $white;
		background-color: $theme;
		&:hover {
			background-color: transparent;
			color: $black;
			.icon {
				position: static;
				opacity: 1;
				fill: $black; } }
		.icon-arrow-right {
			opacity: 0;
			position: absolute; } } }



