@include font(FuturaNewBold, FuturaNewBold-Reg);
@include font(FuturaNewBook, FuturaNewBook-Reg);
@include font(FuturaNewLight, FuturaNewLight-Reg);
@include font(FuturaNewMedium, FuturaNewMedium-Reg);

@mixin bold {
	font-family: FuturaNewBold, Arial, sans-serif;
	font-weight: normal; }

@mixin book {
	font-family: FuturaNewBook, Arial, sans-serif;
	font-weight: normal; }

@mixin light {
	font-family: FuturaNewLight, Arial, sans-serif;
	font-weight: normal; }

@mixin medium {
	font-family: FuturaNewMedium, Arial, sans-serif;
	font-weight: normal; }


