.f-nav {
	column-count: 2;
	column-gap: 3.2rem;
	font-size: 1.2rem;
	@include book;
	@include md {
		column-count: auto;
		column-gap: normal;
		display: flex; }
	@include xs {
		font-size: 1rem;
		flex-wrap: wrap;
		letter-spacing: 0.08rem; }
	&__item {
		margin-bottom: 1.4rem;
		@include md {
			&:not(:last-child) {
				margin-right: 2rem; } }
		@include xs {
			&:not(:last-child) {
				margin-right: 1.7rem; } }
		a {
			text-decoration: none;
			color: $white; } } }
